
html {
  height: 100%;
}
body {
  display: -ms-flexbox;
  display: flex;
  /*align-items: center;*/
  /*justify-content: center;*/
  height: 100%;
}
#app {
  color: #2c3e50;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
  width: 100%;
}
#app a {
  color: #42b983;
