  font-style: normal;
}
.header-button {
  font-size: 0.9em;
  color: #42b983;
}
#header {
  padding: 10px 20px 10px 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
}
.user-operation-panel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-item-align: end;
      align-self: flex-end;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: end;
      align-content: flex-end
}
.action-bar {
  display: -ms-flexbox;
  display: flex;
  margin-left: 10px;
  padding-right: 4px;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-item-align: end;
      align-self: flex-end;
  -ms-flex-align: baseline;
      align-items: baseline;
}
.button-divider {
  margin-left: 10px;
