  color: #ffffff;
  border-width: 0;
  border-radius: 4px;
}
input {
  border: 1px solid #eeeeee;
  padding: 8px;
  margin: 8px 16px 8px 16px;
}
textarea {
  border: 1px solid #eeeeee;
  padding: 8px;
  margin: 8px 16px 8px 16px;
}
#username {
  text-decoration: underline;
}
#user-operation-panel #admin {
  margin-left: 10px;
  padding-right: 4px;
}





